import DomUtil from "./Utilities/DomUtil";
import UrlUtil from "./Utilities/UrlUtil";
import HtmlLoader from "./Utilities/HtmlLoader";
import * as tingle from "tingle.js";
import MakeRequest from "./Utilities/MakeRequest";

export default class App {
  constructor() {}

  execute() {
    try {
      this.setListeners();
    } catch (error) {}
  }

  createUserListener() {
    var form = document.forms.namedItem("createUser");
    if(!form) return;
    form.addEventListener("submit", () => {
      event.preventDefault();

      const addUserUrl = `${new UrlUtil().baseUrl()}/api/user/add`;
      let json = convertFormToJsonString(form);

      new MakeRequest(addUserUrl, "post")
        .send(json)
        .then(() => {
          window.alert("Success");
          form.reset();          
        })
        .catch(err => {
          window.alert("Error");
          console.error("Augh, there was an error!", err.statusText);
        });
    });
  }

  setListeners() {
    this.createUserListener();
    
    var usersRows = document.querySelectorAll(".user");
    const userRowsArray = [].slice
      .call(usersRows);
    userRowsArray.forEach((element: Element, index: number, array: HTMLElement[]) => {
        element.addEventListener(
          "click",
          () => {
            const domUtil = new DomUtil(element);
            const urlUtil = new UrlUtil();
            const userId = domUtil.getDataAttr("userid");
            const userUrl = `${urlUtil.baseUrl()}/api/modal/user?id=${userId}`;
            new HtmlLoader("#userModal").load(userUrl);
            new MakeRequest(userUrl)
              .send()
              .then(data => {
                const modal = new tingle.modal({
                  footer: true,
                  stickyFooter: false,
                  closeMethods: ["overlay", "button", "escape"],
                  closeLabel: "Close",
                  cssClass: ["custom-class-1", "custom-class-2"],
                  beforeClose: () => true
                });
                modal.setContent(data);
                modal.open();
              })
              .catch(err => {
                console.error("Augh, there was an error!", err.statusText);
              });
          },
          false
        );
      });
  }
}

document.onreadystatechange = () => {
  if (document.readyState == "interactive") {
    new App().execute();
  }
};
function convertFormToJsonString(form: HTMLFormElement) {
  const formData = new FormData(form);
  let jsonObject = {};
  for (const [key, value] of formData.entries()) {
    jsonObject[key] = value;
  }
  return JSON.stringify(jsonObject);
}
